@use "../../utils/styles.scss" as s;

.button-block {
  padding: 4px;
  width: 240px;
  height: 224px;
  display: grid;
  grid-template-columns: repeat(3, 72px);
  grid-auto-flow: dense;
  row-gap: 8px;
  column-gap: 8px;
  border-radius: 4px;
  box-shadow: s.$element-shadow;
}


div[data-button-value="9"],
div[data-button-value="6"],
div[data-button-value="3"] {
  grid-column-start: 3;
  grid-column-end: 3;
}

div[data-button-value="8"], div[data-button-value="5"],
div[data-button-value="2"] {
  grid-column-start: 2;
  grid-column-end: 2;
}

div[data-button-value="1"], div[data-button-value="4"],
div[data-button-value="7"] {
  grid-column-start: 1;
  grid-column-end: 1;
}

