@use "../../utils/styles.scss" as s;

.container {
    list-style: none;
    height: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 12px;
    padding: 0;
    margin: 0;
    align-items: center;

    &path {
        fill: s.$button-pressed;
    }

    &svg {
        padding: 0 10px;
    }
}


